.layout-search{
  &__form{
    border-radius: 0px;
    height: auto;
    &-group{
      &:hover,&:focus,&:active,&:visited{
        box-shadow: none !important;
        border: none;
        outline: none;
      }
    }
  }
  &__description{
    font-size: 1.2rem;
  }
  &__shellicon{
    font-size: 1.8rem;
    margin-top: 1rem;
  }
  &__input{
    font-size: 1.2rem;
    background-color: transparent;
    width: 100%;
    box-shadow: none;
    border: none;
    border-bottom: 1px solid #363220;;
    outline: none;
    border-radius: 0px;
    height: auto;
    &:hover,&:focus,&:active,&:visited{
      background-color: transparent;
      box-shadow: none;
      outline: none;
    }
    &:focus, &:active, &:visited{
      border-bottom: 1px solid #363220;;
    }
    @media (min-width: 300px) and (max-width: 992px) {
      font-size: 1.2rem !important;
    }
  }
  &__btn-clear{
    &:hover,&:focus,&:active,&:visited{
      text-decoration: none;
    }
  }
  &__data{
    list-style: none;
    margin: 15px 0px;
    font-family: unquote(map-get($headingtype, font-family));
    font-size: 1.8rem;
    li{
      margin: 15px 0px;
    }
    transition: 0.9s;
    a{
      transition: 0.9s;
      &:hover{
        transition: 0.9s;
        .layout-search__data__entry-date{
          transition: 0.9s;
          background-color: #0000;
        }
      }
    }
    &__entry-date{
      font-size: 0.9rem;
      padding: 5px;
      background-color: #1a1a1a;
      margin-left: 10px;
      border-radius: 0px;
      color: #fff;
    }
    &__not-search{
      padding: 15px 0;
      color: red;
      font-size: 1.7rem;
    }
  }
}
